.about__article {
	display: flex;
	flex-direction: column;
	width: 100%;
	padding: 0 15px;
}

.about__article h3 {
	font-weight: 600;
	font-size: 20px;
	margin-top: 30px;
}

.about__article p {
	line-height: 26px;
	font-size: 16px;
	margin: 0 0 30px;
}

.about__imgbox img {
	max-width: 100%;
}

@media (min-width: 768px) {
	.about__article {
		width: 50%;
	}
}

@media (min-width: 1200px) {
	.about__article {
		width: 25%;
	}
}

.line {
	width: 50px;
	height: 3px;
	background-color: #2a2a2a;
	margin: 0 auto 30px;
}

.line--color {
	background-color: #53a59c;
}

.section__h2 {
	font-family: 'Verdana', Helvetica, Arial, Lucida, sans-serif;
	font-size: 42px;
	color: #3f3d3a;
	line-height: 1.3em;
	text-align: center;
	font-weight: normal;
}

.about {
	width: 100%;
	margin-left: auto;
	margin-right: auto;
	margin-top: 60px;
}

.about__description {
	text-align: center;
	font-family: 'Verdana', Helvetica, Arial, Lucida, sans-serif;
	font-size: 15px;
	letter-spacing: 3px;
	line-height: 1.8em;
	max-width: 700px;
	margin: 15px auto 0;
	padding: 0 15px;
}

.about__columns {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	width: 100%;
	margin-top: 90px;
}

@media (min-width: 768px) {
	.about__columns {
		flex-direction: row;
	}
}

@media (min-width: 1200px) {
	.about {
		width: 1200px;
	}
	.about__columns {
		flex-direction: row;
	}
}

.subscribe {
	width: 100%;
	background-color: #9ad8da;
	padding: 60px 15px;
	text-align: center;
	margin: 30px 0;
}

.subscribe__title {
	color: #333333;
	margin: 0;
	padding-bottom: 15px;
	font-weight: 700;
	font-size: 20px;
	line-height: 20px;
}

#subscribe__form {
	border-top: 1px solid #797979;
	border-bottom: 1px solid #797979;
	padding: 30px 0;
}

.subscribe__form-group {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.subscribe__form-group label {
	color: #000000;
}

.required {
	color: #f71010;
}

.form__control {
	width: 100%;
	border: none;
	margin: 15px 0;
	height: 30px;
	border-radius: 4px;
}

.subscribe__button {
	margin: 0 auto;
	background-color: white;
	border: none;
	padding: 15px 30px;
	border-radius: 64px;
	margin-top: 30px;
	letter-spacing: 2px;
	color: #3f3d3a;
	font-size: 15px;
	font-weight: 700;
}

@media (min-width: 768px) {
	.subscribe__container {
		width: 70%;
		margin: auto;
	}
}

@media (min-width: 1200px) {
	.subscribe {
		border-radius: 50px;
		margin-left: -150px;
	}
	.subscribe__container {
		width: 500px;
		margin: auto;
	}
}

.partnership {
	background-color: #d2d0cd;
	padding: 60px 0;
	margin: 60px auto 0;
	text-align: center;
}

.partnership__info {
	font-family: 'Verdana', Helvetica, Arial, Lucida, sans-serif;
	font-size: 15px;
	letter-spacing: 3px;
	line-height: 1.8em;
	max-width: 700px;
	margin: 15px auto 0;
	padding: 0 15px 30px;
	text-transform: uppercase;
}

.partnership__link {
	color: #53a59c;
	font-family: 'Verdana', Helvetica, Arial, Lucida, sans-serif;
	font-size: 15px;
	letter-spacing: 3px;
	line-height: 1.8em;
	text-transform: uppercase;
}

.bottom__triangle {
	display: none;
	width: 0;
	height: 0;
	border-left: 50vw solid transparent;
	border-right: 50vw solid transparent;
	border-top: 8vw solid #d2d0cd;
}

@media (min-width: 1200px) {
	.bottom__triangle {
		display: block;
	}
}

